import React, { Component } from 'react'

export default class Chars extends Component {
  constructor(props){
    super(props);
    this.state = {
      items: [],
    };
  }

  componentDidMount() {
    fetch('https://swapi.dev/api/people/?page=1')
    .then(res => res.json())
    .then(res => {
        this.setState({
          items: res.results,
        });
      });
  }


  render() {
    const {items} = this.state;
    return (
      <ul> <h1>Characters:</h1>
        {items.map(io => (
          <li key={io.name}>
          <p>Name : {io.name}</p>
          <p>Birth year : {io.birth_year}</p>
          <p>Gender : {io.gender}</p>
          <p>Eye color : {io.eye_color}</p>
          <p>Height : {io.height}</p>
          <p>Mass : {io.mass}</p>
          <p>Skin color : {io.skin_color}</p>
          </li>
        ))}
      </ul>
    )
  }
}